body{
    background-image: url("https://images.unsplash.com/photo-1538137524007-21e48fa42f3f?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=ac9fa0975bd2ebad7afd906c5a3a15ab&auto=format&fit=crop&w=1834&q=80");
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-color: #464646;
    width: 100%;
    padding:10px;
}

.navbar{
    width: auto;
    background-image: linear-gradient(160deg, #6C5C4C 0%, #dfe4ea 100%);
}

.listKanban{
    display:flex;
    flex-direction: row;
    align-items: flex-start;
    color: black;
    .kanbanContainer{
        width:21em;
        min-height: 100px;
        max-height: 500px;
        background-image: linear-gradient(160deg, #6C5C4C 0%, #dfe4ea 100%);
        margin:10px 7px 0px 7px;
        // margin-top: 10px;
        padding-bottom:10px;
        border-radius: 10px;
        border: 1px solid #dfe4ea;
        display:flex;
        flex-direction: column;
        align-items: center;
        align-content: flex-start;
        opacity: 80%;
        .containerKartu{
            height:85%;
            overflow-y: scroll;
        }
            .card{
                margin-bottom: 4px;
                display: flex;
                justify-content: center;
                padding:3px;
                width:19.5em;
                border-radius: 10px;
                .tombol{
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    .kanan{
                        display: flex;
                        flex-direction: row;
                        justify-content: flex-end;
                    }
                    .kiri{
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                    }
                    .tengah{
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                    }
                }
            }
        
        .addTask{
            cursor: pointer;
        }
    }
}
$base-bgcolor: #354152;
$base-color: #7e8ba3;
$base-font-weight: 300;
$base-font-size: 1rem;
$base-line-height: 1.5;
$base-font-family: Helvetica Neue;
$base-font-family-fallback: sans-serif;

* {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  background-color: $base-bgcolor;
  color: $base-color;
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family, $base-font-family-fallback;
  margin: 0;
  min-height: 100%;
}

.align {
  align-items: center;
  display: flex;
  flex-direction: row;

  &__item {

    &--start {
      align-self: flex-start;
    }

    &--end {
      align-self: flex-end;
    }

  }

}



$input-placeholder-color: #7e8ba3;

input {
  border: 0;
  font: inherit;

  &::placeholder {
    color: $input-placeholder-color;
  }

}

.form {

  &__field {
    margin-bottom: 1rem;
  }

  input {
    outline: 0;
    padding: .5rem 1rem;

    &[type="email"],
    &[type="password"] {
      width: 100%;
    }

  }

}

$grid-max-width: 25rem;
$grid-width: 100%;

.grid {
  margin: 0 auto;
  max-width: $grid-max-width;
  width: $grid-width;
  height:"40%"
}

h2 {
  font-size: 2.75rem;
  font-weight: 100;
  margin: 0 0 1rem;
  text-transform: uppercase;
}

svg {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

$link-color: #7e8ba3;

a {
  color: $link-color
}

.login {
  box-shadow: 0 0 250px #000;
  text-align: center;
  padding: 4rem 2rem;
  background-color:#dfe4ea;
  border-radius: 7%;

  input {
    border: 1px solid #242c37;
    border-radius: 999px;
    background-color: transparent;
    text-align: center;

    &[type="email"],
    &[type="password"] {
      background-repeat: no-repeat;
      background-size: 1.5rem;
      background-position: 1rem 50%;
    }

    &[type="email"] {
      background-image: url('data:image/svg+xml;charset=UTF-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="#242c37"><path d="M256.017 273.436l-205.17-170.029h410.904l-205.734 170.029zm-.034 55.462l-205.983-170.654v250.349h412v-249.94l-206.017 170.245z"/></svg>');
    }

    &[type="password"] {
      background-image: url('data:image/svg+xml;charset=UTF-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="#242c37"><path d="M195.334 223.333h-50v-62.666c0-61.022 49.645-110.667 110.666-110.667 61.022 0 110.667 49.645 110.667 110.667v62.666h-50v-62.666c0-33.452-27.215-60.667-60.667-60.667-33.451 0-60.666 27.215-60.666 60.667v62.666zm208.666 30v208.667h-296v-208.667h296zm-121 87.667c0-14.912-12.088-27-27-27s-27 12.088-27 27c0 7.811 3.317 14.844 8.619 19.773 4.385 4.075 6.881 9.8 6.881 15.785v22.942h23v-22.941c0-5.989 2.494-11.708 6.881-15.785 5.302-4.93 8.619-11.963 8.619-19.774z"/></svg>');
    }

    &[type="submit"] {
        background-image: linear-gradient(160deg, #6C5C4C 0%, #dfe4ea 100%);
        border: #dfe4ea;
      color: #fff;
      margin-bottom: 6rem;
      width: 100%;
    }

  }

}
